import {Component} from '@angular/core';
import {IonicPage, MenuController, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {ConfigProvider} from "../../providers/config/config";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {ErrorMessageProvider} from "../../providers/error-message/error-message";
import {AuthServiceProvider} from "../../providers/auth-service/auth-service";
import {MenuServiceProvider} from "../../providers/menu-service/menu-service";
import {InAppBrowser} from "@ionic-native/in-app-browser";
import {Diagnostic} from "@ionic-native/diagnostic";
import {LocationProvider} from "../../providers/location/location";
import {AlertProvider} from "../../providers/alert/alert";
import {Network} from "@ionic-native/network";
import {Idle} from "@ng-idle/core";
import {RemittanceProvider} from "../../providers/remittance/remittance";
import {AuthenticationProvider} from "../../providers/authentication/authentication";


@IonicPage()
@Component({
  selector: 'page-user-login01',
  templateUrl: 'user-login01.html',
})
export class UserLogin01Page {
  appData;
  loginError01;
  tokenResponse = {};
  account = {userID: '', passWD: '', isChecked: false};
  loginUserId;
  type;//for location service
  gpsEnableCheck;
  passwordShown;
  eyeIcon;
  version;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil: CryptoParamUtilProvider,
              public storage: LocalStorageProvider,
              public maskToken: MaskTokenProvider,
              public tokenService: TokenServiceProvider,
              public menuService: MenuServiceProvider,
              public messageProvider: ErrorMessageProvider,
              public authService: AuthServiceProvider,
              public remittanceService: RemittanceProvider,
              public config: ConfigProvider,
              public menu: MenuController,
              public iab: InAppBrowser,
              public locationService: LocationProvider,
              public diagnostic: Diagnostic,
              public alertProvider: AlertProvider,
              public network: Network,
              public idle: Idle,
              public authProvider: AuthenticationProvider) {

    this.version = this.config.iSmartVersion;
    this.passwordShown = false;
    this.eyeIcon = "eye";

    this.gpsEnableCheck = this.config.gpsCheckEnabled;
    this.init();
  }

  init = function () {
    this.menu.enable(false);

    let remInfo = this.storage.retrieveFromStorage("remInfo");

    if (remInfo == null) {
      let remInfo = {
        status: 0
      };
      this.storage.insertIntoStorage("remInfo", remInfo);
      this.account.isChecked = false;
      this.account.userID = "";
    }

    else {

      if (remInfo.status == 1) {
        let appData = this.storage.retrieveFromStorage("appData");
        this.account.isChecked = true;
        this.account.userID = this.cryptoUtil.getDecryptVal(appData.userId);
      }
      else {
        this.account.isChecked = false;
        this.account.userID = "";
      }
    }
  };

  togglePassword(input): any {
    input.type = input.type == 'password' ? 'text' : 'password';
    if (this.passwordShown) {
      this.passwordShown = false;
      this.eyeIcon = "eye";
    }
    else {
      this.passwordShown = true;
      this.eyeIcon = "eye-off";
    }
  };

  public userLogin01Action() {

    if (this.network.type == "none") {
      let alertHead = "Alert !";
      let message = "<center>Internet Disconnected. Please connect it first.</center>";
      this.alertProvider.showAlert(alertHead, message, 10000);
      return;
    }

    this.loginError01 = "";
    let emailExp = new RegExp("^[A-Za-z0-9\\._%'\\-\\*]+@[A-Za-z0-9.-]+\\.[a-zA-Z]{2,4}$");
    let flag = true;

    if (this.account.userID == "" || typeof(this.account.userID) == "undefined") {
      this.loginError01 = "UserID can not be blank.";
      flag = false;
      this.account.passWD = "";
      return;
    }
    if (!emailExp.test(this.account.userID.trim())) {
      this.loginError01 = "UserID is incorrect.";
      flag = false;
      this.account.passWD = "";
      return;
    }

    if (this.account.userID.length > 100) {
      this.loginError01 = "UserID invalid, UserID maximum length exceed.";
      flag = false;
      this.account.passWD = "";
      return;
    }


    if (this.account.passWD == "" || typeof(this.account.passWD) == "undefined") {
      this.loginError01 = "Password can not be blank.";
      flag = false;
      return;
    }
    if (this.account.passWD.length > 50) {
      this.loginError01 = "Password invalid, Password maximum length exceed.";
      flag = false;
      this.account.passWD = "";
      return;
    }

    if (flag) {
      this.loginUserId = this.account.userID.trim().toLowerCase();

      // this.tokenService.getRefreshToken(this.loginUserId, this.account.passWD)
      //   .then(data => {
      //     this.tokenResponse = data;
      //     this.onTokenGenComplete(this.tokenResponse);
      //     // console.log(this.tokenResponse);
      //   });
      let encProfileUserID = this.cryptoUtil.getEncryptVal(this.loginUserId);
      this.authProvider.checkProfileCurrentStatus(encProfileUserID).then(data => {
        this.onProfileLockCheckComplete(data);
      });
    }
  }

  onProfileLockCheckComplete = function (data) {

    if (data.status == 0) {
      this.loginError01 = "Service unreachable, please try again later.";
    }

    else if (data.statusCode == 404) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else if (data.signInResponse.status != null) {
      let retStatus = data.signInResponse.status;

      if (retStatus == 100) {

        this.tokenService.getRefreshToken(this.loginUserId, this.account.passWD)
          .then(data => {
            this.tokenResponse = data;
            this.onTokenGenComplete(this.tokenResponse);
            // console.log(this.tokenResponse);
          });
      }

      else if (retStatus == 999) {
        this.loginError01 = data.signInResponse.note;
      }
    }

    else {
      this.loginError01 = "System Error, Please Contact with Administrator.";
    }
  };

  onTokenGenComplete = function (data) {

    if (data.tokenType == "bearer") {
      this.appData = this.storage.retrieveFromStorage('appData');

      if (this.appData == null) {
        let date = new Date();
        let uuid01 = Math.floor(Math.random() * 900000000000) + 100000000000;
        let uuid02 = Math.floor(Math.random() * 900000000000) + 100000000000;
        let millSec = date.getMilliseconds();
        //Date format was 'yyyyMMddHHmmss'
        let today = date.getFullYear().toString() + (date.getMonth() + 1).toString() +
          date.getDate().toString() + date.getHours().toString() + date.getMinutes().toString() +
          date.getMinutes().toString();

        let applicationId = uuid01.toString() + uuid02.toString() + "-" + today + millSec;
        let encAppId = this.cryptoUtil.getEncryptVal(applicationId);

        //Generate appId and Update Status
        let savedData = {
          status: 801,
          appId: encAppId
        };
        this.storage.insertIntoStorage('appData', savedData);
      }

      let dbVal = this.storage.retrieveFromStorage('appData');
      dbVal.token = this.maskToken.getMaskVal(data.value);
      dbVal.refreshToken = this.maskToken.getMaskVal(data.refreshToken.value);
      dbVal.userId = this.cryptoUtil.getEncryptVal(this.loginUserId);
      this.storage.insertIntoStorage('appData', dbVal);

      //SETTING UP USER ID IS SAVED OR NOT
      if (this.account.isChecked) {
        let remInfo = {
          status: 1
        };
        this.storage.insertIntoStorage("remInfo", remInfo);
      }
      else {
        let noRemInfo = {
          status: 0
        };
        this.storage.insertIntoStorage("remInfo", noRemInfo);
      }

      this.idle.watch();
      //REQUEST FOR OTP GENERATION TO SERVER
      if (dbVal.status == 801) {
        // console.log("STATUS : 801");
        this.authService.generateOTP(dbVal.userId, dbVal.appId, data.value)
          .then(data => {
            let otpResponse = data;
            // console.log(otpResponse);
            this.onOTPGenComplete(otpResponse);
          });
      }
      //REQUEST FOR ACTIVATION CHECKING TO SERVER
      if (dbVal.status == 802) {
        let param = {'userId': this.loginUserId, 'appId': dbVal.appId, 'token': dbVal.token};
        this.navCtrl.push('UserLogin03Page', {"param": param});
      }

      // User Already Verified
      //REQUEST FOR GENERATE MANU AND PRIVILEGE TO SERVER
      if (dbVal.status == 800) {
        let appVersion = this.config.iSmartVersion;
        let encAppVersion = this.cryptoUtil.getEncryptVal(appVersion);

        this.menuService.generateMenu(dbVal.userId, dbVal.appId, data.value, encAppVersion)
          .then(data => {
            this.onMenuGenComplete(data);
          });
      }
    }

    //If token generation fail then display the error message
    else {
      this.loginError01 = this.messageProvider.getErrorMessage(data.status);
      this.account.passWD = "";
    }
  };

  onOTPGenComplete = function (data) {
    //$log.info("OTP Generation Complete Function " + JSON.stringify(data));
    //IF OTP GENERATION IS SUCCESSFUL
    if (data.enrollStatus == 1) {
      let dbVal = this.storage.retrieveFromStorage('appData');
      let param = {
        'userId': this.loginUserId,
        'appId': dbVal.appId,
        'token': dbVal.token
      };
      // alert("param value in 01 :"+JSON.stringify(param));
      this.navCtrl.push('UserLogin02Page', {"param": param});
    }

    else if (data.enrollStatus == 0) {
      this.loginError01 = data.note;
      this.account.passWD = "";
    }

    else {
      this.loginError01 = "Internal Error. Please Contact your administrator.";
      this.account.passWD = "";
    }
  };

  onMenuGenComplete = function (data) {

    //IF MENU GENERATION IS SUCCESSFUL
    if (data.loginStatus == 1 || data.loginStatus == 2) {
      let menus = data.privMenus;
      let accounts = data.accounts;
      let operators = data.rechargeOperators;

      let welcomeVal = {
        fName: data.fName,
        lName: data.lName,
        welcomeText: data.welcomeText,
        transactional: data.transactional,
        currentAppVersion: data.currentAppVersion
      };

      let encWelcomeVal = this.cryptoUtil.getEncryptVal(JSON.stringify(welcomeVal));
      this.storage.insertIntoStorage('welcomeVal', encWelcomeVal);

      let encMenu = this.cryptoUtil.getEncryptVal(JSON.stringify(menus));
      let encAllAccounts = this.cryptoUtil.getEncryptVal(JSON.stringify(accounts));

      this.storage.insertIntoStorage('privMenus', encMenu);
      this.storage.insertIntoStorage('accounts', encAllAccounts);
      this.storage.insertIntoStorage('passPolicy', data.passPolicyText);
      this.storage.insertIntoStorage('rechargeOperator', operators);


      if (data.loginStatus == 1) {
        this.navCtrl.setRoot('WelcomePage', {});
      }
      if (data.loginStatus == 2) {
        this.navCtrl.push('PasswordExpirePage', {});
      }
    }

    else if (data.loginStatus == 0) {
      this.loginError01 = "UserID or Password or Device info or all are incorrect.";
      this.account.passWD = "";

    }

    else {
      this.loginError01 = this.messageProvider.getErrorMessage(data.status);
      this.account.passWD = "";
    }
  };

  goToLocationByType = function (locType) {
    this.loginError01 = "";
    if (this.network.type == "none") {
      let alertHead = "Alert !";
      let message = "<center>Internet Disconnected. Please connect it first.</center>";
      this.alertProvider.showAlert(alertHead, message, 10000);
      return;
    }

    this.type = this.locationService.getLocationDescription(locType);
    let encLocType = this.cryptoUtil.getEncryptVal(locType);
    if (this.gpsEnableCheck) {
      this.diagnostic.isLocationEnabled()
        .then((state) => {
          if (state == true) {
            this.locationService.getActiveLocationByType(encLocType)
              .then(data => {
                this.locationFetchComplete(data);
              });
          }
          else {
            let alertHead = "Alert !";
            let message = "<center>Device location access  disabled. Please enable it and try again.</center>";
            this.alertProvider.showAlert(alertHead, message, 10000);
          }
        }).catch(e => console.error(e));
    }

    else {
      this.locationService.getActiveLocationByType(encLocType)
        .then(data => {
          this.locationFetchComplete(data);
        });
    }
  };

  locationFetchComplete = function (data) {

    if (data.status == 0) {
      this.loginError01 = "Service unreachable, please try again later.";
    }

    else if (data.statusCode == 404) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else if (data.statusCode == 1) {
      this.navCtrl.push('LocationOuter01Page', {'type': this.type, 'locationDetails': JSON.stringify(data.object)})
    }

    else if (data.statusCode == 0) {
      this.loginError01 = data.note;
      return;
    }

    else {
      this.loginError01 = "System Error, Please Contact with Administrator.";
    }
  };

  goToRegistration = function () {
    if (this.network.type == "none") {
      let alertHead = "Alert !";
      let message = "<center>Internet Disconnected. Please connect it first.</center>";
      this.alertProvider.showAlert(alertHead, message, 10000);
      return;
    }
    this.iab.create(this.config.getRegistrationURL(), '_blank', {
      location: 'no',
      footer: 'yes',
      clearsessioncache: 'yes',
      clearcache: 'yes'
    });
  };

  goToRemittancePage = function () {
    if (this.network.type == "none") {
      let alertHead = "Alert !";
      let message = "<center>Internet Disconnected. Please connect it first.</center>";
      this.alertProvider.showAlert(alertHead, message, 10000);
      return;
    }

    let deviceID = this.maskToken.getDeviceID();
    // console.log(deviceID);
    let encDeviceID = this.cryptoUtil.getEncryptVal(deviceID);
    let providedHash = this.cryptoUtil.getSha256EncryptedValue(deviceID + this.cryptoUtil.salt);
    this.remittanceService.getExchangeHouseList(encDeviceID, providedHash)
      .then(data => {
        this.exHouseFetchComplete(data);
      });

  };

  exHouseFetchComplete = function (data) {
    // console.log("data :" + JSON.stringify(data));
    this.loginError01 = "";
    if (data.status == 0) {
      this.loginError01 = "Service unreachable, please try again later.";
    }

    else if (data.statusCode == 404) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else if (data.statusCode == 1) {
      // console.log(data.object);
      this.navCtrl.push('RemittanceInfo02Page', {'exchangeHouseList': JSON.parse(data.object), 'navType': "1"})
    }

    else if (data.statusCode == 0) {
      if (data.object == null) {
        this.loginError01 = "System error. Contact with administrator.(Fail to load data)";
      }

      else {
        this.loginError01 = data.note;
      }
      return;
    }

    else {
      this.loginError01 = "System Error, Please Contact with Administrator.";
    }
  };

  GenerateCaptchaAction = function () {
    this.loginError01 = "";
    if (this.network.type == "none") {
      let alertHead = "Alert !";
      let message = "<center>Internet Disconnected. Please connect it first.</center>";
      this.alertProvider.showAlert(alertHead, message, 10000);
      return;
    }

    this.navCtrl.push('ResetPassword01Page', {});
  };


  ionViewDidLoad() {

  }


}



